import "regenerator-runtime";
import "../styles/main.css";
import Swal from "sweetalert2";
import "../scripts/components/animasi";

const RESTAURANT_API_ENDPOINT = "https://restaurant-api.dicoding.dev";

async function fetchRestaurantList() {
  showLoader();
  try {
    const response = await fetch(`${RESTAURANT_API_ENDPOINT}/list`);
    if (!response.ok) {
      throw new Error("Gagal mendapatkan data restoran");
    }
    const jsonData = await response.json();
    return jsonData.restaurants;
  } catch (error) {
    Swal.fire({
      icon: "error",
      title: "Terjadi Kesalahan",
      text: "Gagal mendapatkan data restoran. Silakan coba lagi.",
    });
    console.error("Error:", error);
    return [];
  } finally {
    hideLoader();
  }
}

class RestaurantList extends HTMLElement {
  async connectedCallback() {
    this.innerHTML = "<div>Memuat halaman...</div>";

    const restaurantList = await fetchRestaurantList();
    this.renderList(restaurantList);
  }

  renderList(restaurants) {
    this.innerHTML = `<div class="restaurant-grid"></div>`;
    const gridContainer = this.querySelector(".restaurant-grid");

    restaurants.forEach((restaurant) => {
      const restaurantElement = document.createElement("div");
      restaurantElement.classList.add("restaurant-item");

      restaurantElement.innerHTML = `
      <div class="list_item">
      <img 
        class="list_item_thumb" 
        src="${RESTAURANT_API_ENDPOINT}/images/medium/${restaurant.pictureId}" 
        alt="${restaurant.name}" 
        loading="lazy"
      />
      <div class="city">${restaurant.city}</div>
      <div class="list_item_content">
        <div class="list_item_rating">
          <span class="star-icon">&#9733;</span> 
          ${restaurant.rating}
        </div>
        <h1 class="list_item_title">
        <a href="#detail-${restaurant.id}" data-id="${restaurant.id}">${restaurant.name}</a>
        </h1>
        <div class="list_item_desc">${restaurant.description.slice(0, 150)}...</div>
      </div>
    </div>
  `;

      gridContainer.appendChild(restaurantElement);
    });
  }
}

customElements.define("restaurant-list", RestaurantList);

export function getRestaurantIdFromHash() {
  const hash = window.location.hash;
  if (hash.startsWith("#detail-")) {
    return hash.split("-")[1];
  }
  return null;
}

document.addEventListener("DOMContentLoaded", async () => {
  const RestaurantList = document.querySelector("#RestaurantList");
  const restaurantId = getRestaurantIdFromHash();
  console.log("RestaurantList Display:", getComputedStyle(RestaurantList).display);
  console.log("Grid Columns:", getComputedStyle(RestaurantList).gridTemplateColumns);

  if (restaurantId) {
    await fetchRestaurantDetail(restaurantId);
  } else {
    const restaurantListElement = document.createElement("restaurant-list");
    RestaurantList.appendChild(restaurantListElement);
  }

  async function handleHashChange() {
    showLoader();
    const hash = window.location.hash;
    const restaurantId = getRestaurantIdFromHash();
    const restaurantListElement = document.querySelector("#RestaurantList");
    const detailElement = document.querySelector("#restaurantDetail");
    const favoriteRestaurantList = document.querySelector("#favoriteRestaurantList");
    restaurantListElement.style.display = "none";
    detailElement.style.display = "none";
    favoriteRestaurantList.style.display = "none";
    if (window.location.hash === "") {
      await renderRestaurantList();
    }
    if (hash === "#favorite") {
      const favoriteRestaurantListElement = document.createElement("favorite-restaurant-list");
      favoriteRestaurantList.innerHTML = "";
      favoriteRestaurantList.appendChild(favoriteRestaurantListElement);

      restaurantListElement.style.display = "none";
      detailElement.style.display = "none";
      favoriteRestaurantList.style.display = "grid";

      hideLoader();
    } else if (hash.startsWith("#detail-")) {
      const restaurantId = getRestaurantIdFromHash();
      await fetchRestaurantDetail(restaurantId);
      restaurantListElement.style.display = "none";
      detailElement.style.display = "block";
      favoriteRestaurantList.style.display = "none";

      hideLoader();
    } else {
      restaurantListElement.style.display = "grid";
      detailElement.style.display = "none";
      favoriteRestaurantList.style.display = "none";
    }
    if (restaurantId) {
      const restaurantDetail = await fetchRestaurantDetail(restaurantId);

      if (restaurantDetail) {
        detailElement.innerHTML = "";
        const restaurantDetailElement = document.createElement("restaurant-detail");
        restaurantDetailElement.setAttribute("restaurant-id", restaurantId);

        detailElement.appendChild(restaurantDetailElement);
        detailElement.style.display = "block";
        restaurantListElement.style.display = "none";

        hideLoader();
      } else {
        console.error("Detail restoran tidak ditemukan");
        window.location.hash = "";
      }
    } else {
      restaurantListElement.style.display = "none";
      favoriteRestaurantList.style.display = "grid";
      detailElement.style.display = "none";

      hideLoader();
    }
  }

  window.addEventListener("hashchange", handleHashChange);
  document.addEventListener("DOMContentLoaded", async () => {
    console.log("Page loaded. Current hash:", window.location.hash);
    await handleHashChange();
  });
});

async function renderContentBasedOnHash() {
  showLoader();
  const hash = window.location.hash;
  const restaurantListElement = document.querySelector("#RestaurantList");
  const detailElement = document.querySelector("#restaurantDetail");
  const favoriteRestaurantList = document.querySelector("#favoriteRestaurantList");

  if (hash.startsWith("#detail-")) {
    const restaurantId = getRestaurantIdFromHash();
    if (restaurantId) {
      const restaurant = await fetchRestaurantDetail(restaurantId);
      if (restaurant) {
        detailElement.innerHTML = "";
        const restaurantDetailElement = document.createElement("restaurant-detail");
        restaurantDetailElement.setAttribute("restaurant-id", restaurantId);
        detailElement.appendChild(restaurantDetailElement);

        detailElement.style.display = "block";
        restaurantListElement.style.display = "none";
        hideLoader();
      } else {
        console.error("Detail restoran tidak ditemukan");
      }
    }
  } else {
    restaurantListElement.style.display = "grid";
    detailElement.style.display = "none";
    hideLoader();
  }
  if (hash === "#favorite") {
    showLoader();

    const favoriteRestaurantListElement = document.createElement("favorite-restaurant-list");
    favoriteRestaurantList.innerHTML = "";
    favoriteRestaurantList.appendChild(favoriteRestaurantListElement);

    restaurantListElement.style.display = "none";
    detailElement.style.display = "none";
    favoriteRestaurantList.style.display = "block";
    hideLoader();
  } else if (hash.startsWith("#detail-")) {
    const restaurantId = getRestaurantIdFromHash();
    await fetchRestaurantDetail(restaurantId);
    restaurantListElement.style.display = "none";
    detailElement.style.display = "block";
    favoriteRestaurantList.style.display = "none";
    hideLoader();
  } else {
    restaurantListElement.style.display = "grid";
    detailElement.style.display = "none";
    favoriteRestaurantList.style.display = "none";
    hideLoader();
  }
}

window.addEventListener("hashchange", renderContentBasedOnHash);

const renderRestaurantList = async () => {
  const restaurantListElement = document.querySelector("#RestaurantList");
  if (restaurantListElement) {
    const restaurantList = await fetchRestaurantList();
    restaurantListElement.innerHTML = "";
    const restaurantListComponent = document.createElement("restaurant-list");
    restaurantListComponent.renderList(restaurantList);
    restaurantListElement.appendChild(restaurantListComponent);
  }
};

document.addEventListener("DOMContentLoaded", async () => {
  console.log("Page loaded. Current hash:", window.location.hash);
  await renderContentBasedOnHash();
});

document.addEventListener("click", async (event) => {
  const favoriteButton = event.target.closest("#favoriteButton");

  if (favoriteButton) {
    const restaurantId = favoriteButton.getAttribute("data-id");
    const isFavorited = await isRestaurantFavorited(restaurantId);

    if (isFavorited) {
      await removeRestaurantFromFavorites(restaurantId);
      favoriteButton.classList.remove("favorited");
      favoriteButton.innerHTML = '<i class="far fa-heart"></i>';
    } else {
      const restaurant = await fetchRestaurantDetail(restaurantId);

      if (restaurant) {
        await addRestaurantToFavorites(restaurant);
        favoriteButton.classList.add("favorited");
        favoriteButton.innerHTML = '<i class="fas fa-heart"></i>';
      }
    }
  }
});

document.addEventListener("click", (event) => {
  const favoriteButton = event.target.closest("#favoriteButton");
  if (favoriteButton) {
    console.log("Tombol favorite diklik:", favoriteButton.getAttribute("data-id"));
  }
});

const fetchRestaurantDetail = async (restaurantId) => {
  showLoader();
  try {
    const response = await fetch(`${RESTAURANT_API_ENDPOINT}/detail/${restaurantId}`);
    if (!response.ok) {
      throw new Error("Gagal mendapatkan detail restoran");
    }

    const data = await response.json();
    return data.restaurant;
  } catch (error) {
    console.error("Gagal mendapatkan detail restoran:", error);
    return null;
  } finally {
    hideLoader();
  }
};

class RestaurantDetail extends HTMLElement {
  constructor() {
    super();
    this.restaurantId = null;
  }

  async connectedCallback() {
    this.restaurantId = this.getAttribute("restaurant-id");
    const restaurant = await fetchRestaurantDetail(this.restaurantId);

    if (restaurant) {
      console.log("Menampilkan detail restoran:", restaurant);
      await this.renderRestaurantDetail(restaurant);
    }
  }

  async renderRestaurantDetail(restaurant) {
    console.log("Mengatur konten untuk detail restoran");
    const categories = restaurant.categories.map((category) => category.name).join(", ");
    const foods = restaurant.menus.foods.map((food) => food.name).join(", ");
    const drinks = restaurant.menus.drinks.map((drink) => drink.name).join(", ");
    const reviewsContent = restaurant.customerReviews
      .map(
        (review) => `
      <div class="customer-review">
        <p><strong>${review.name}</strong> pada ${review.date}</p>
        <p>${review.review}</p>
      </div>
    `
      )
      .join("");

    const isFavorited = await isRestaurantFavorited(this.restaurantId);
    const favoriteButtonIcon = isFavorited ? "fas fa-heart" : "far fa-heart";

    const backButton = `
      <button id="backButton" class="back-button" aria-label="Kembali ke daftar restoran"><svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
      <path d="M19 12H5" stroke="#fff" stroke-width="5" stroke-linecap="round" stroke-linejoin="round"/>
      <path d="M12 19L5 12L12 5" stroke="#fff" stroke-width="5" stroke-linecap="round" stroke-linejoin="round"/>
    </svg></button>
    `;

    const favoriteButton = `
      <button class="favorite-button" id="favoriteButton" data-id="${this.restaurantId}">
        <i class="${favoriteButtonIcon}"></i>
      </button>
    `;

    const reviewForm = `
      <div id="reviewFormContainer">
        <h3>Tambahkan Ulasan</h3>
        <form id="reviewForm">
          <input type="hidden" name="restaurantId" value="${this.restaurantId}">
          <div>
            <label for="reviewName">Nama:</label>
            <input type="text" id="reviewName" name="name" required>
          </div>
          <div>
            <label for="reviewText">Ulasan:</label>
            <textarea id="reviewText" name="review" required></textarea>
          </div>
          <button type="submit">Kirim Ulasan</button>
        </form>
      </div>
    `;

    this.innerHTML = `
      <h1>Detail Restoran</h1>
      ${backButton} <!-- Tambahkan tombol kembali -->
      <img src="${RESTAURANT_API_ENDPOINT}/images/medium/${restaurant.pictureId}" alt="${restaurant.name}">
      <h2>${restaurant.name}</h2>
      <div class="detailatas">
        <p><strong>Alamat:</strong> ${restaurant.address}</p>
        <p><strong>Kota:</strong> ${restaurant.city}</p>
        <p><strong>Kategori:</strong> ${categories}</p>
        <p><strong>Description:</strong> ${restaurant.description}</p>
        <p><strong>Menu Makanan:</strong> ${foods}</p>
        <p><strong>Menu Minuman:</strong> ${drinks}</p>
        ${favoriteButton}
      </div>
      ${reviewForm}
      <div class="ulasanresto">${reviewsContent}</div>
    `;

    this.querySelector("#backButton").addEventListener("click", () => {
      window.location.hash = "";
      location.reload();
    });

    this.querySelector("#favoriteButton").addEventListener("click", async (event) => {
      const button = event.target;
      const isFavorited = await isRestaurantFavorited(this.restaurantId);

      if (isFavorited) {
        await removeRestaurantFromFavorites(this.restaurantId);
        button.classList.remove("favorited");
        button.innerHTML = '<i class="far fa-heart"></i>';

        Swal.fire({
          icon: "warning",
          title: "Tidak di Favoritkan!!",
          text: "Restoran telah di hapus dari favorit.",
        });
      } else {
        await addRestaurantToFavorites({ id: this.restaurantId });
        button.classList.add("favorited");
        button.innerHTML = '<i class="fas fa-heart"></i>';

        Swal.fire({
          icon: "success",
          title: "Di Favoritkan!!",
          text: "Restoran telah di favoritkan.",
        });
      }
    });

    this.querySelector("#reviewForm").addEventListener("submit", async (event) => {
      event.preventDefault();
      showLoader();

      const formData = new FormData(event.target);
      const reviewData = {
        id: formData.get("restaurantId"),
        name: formData.get("name"),
        review: formData.get("review"),
      };

      try {
        const reviewResponse = await fetch(`${RESTAURANT_API_ENDPOINT}/review`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(reviewData),
        });

        if (reviewResponse.ok) {
          const responseData = await reviewResponse.json();
          const newReview = responseData.customerReviews.at(-1);

          this.querySelector(".ulasanresto").insertAdjacentHTML(
            "beforeend",
            `
                <div class="customer-review">
                  <p><strong>${newReview.name}</strong> pada ${newReview.date}</p>
                  <p>${newReview.review}</p>
                </div>
              `
          ); // Kosongkan formulir ulasan
          event.target.reset();

          Swal.fire({
            icon: "success",
            title: "Ulasan Berhasil",
            text: "Ulasan Anda telah dikirim.",
          }); // Pesan keberhasilan

          console.log("Ulasan berhasil dikirim dan ditambahkan ke halaman.");
          hideLoader();
        } else {
          console.error("Gagal mengirim ulasan.");
        }
      } catch (error) {
        console.error("Error mengirim ulasan:", error);
        hideLoader();
      }
    });
  }
}
customElements.define("restaurant-detail", RestaurantDetail);

const DATABASE_NAME = "restaurantDB";
const OBJECT_STORE_NAME = "favorites";

const openDatabase = () => {
  return new Promise((resolve, reject) => {
    const request = indexedDB.open(DATABASE_NAME, 1);

    request.onupgradeneeded = (event) => {
      const db = event.target.result;
      if (!db.objectStoreNames.contains(OBJECT_STORE_NAME)) {
        db.createObjectStore(OBJECT_STORE_NAME, { keyPath: "id" });
      }
    };

    request.onsuccess = (event) => {
      resolve(event.target.result);
    };

    request.onerror = (event) => {
      Swal.fire({
        icon: "error",
        title: "Gagal Membuka Database",
        text: "Terjadi kesalahan saat membuka database. Silakan coba lagi.",
      });
      reject(event.target.error);
    };
  });
};

export const addRestaurantToFavorites = async (restaurant) => {
  const db = await openDatabase();
  const transaction = db.transaction(OBJECT_STORE_NAME, "readwrite");
  const objectStore = transaction.objectStore(OBJECT_STORE_NAME);

  try {
    const restaurantData = {
      id: restaurant.id,
      name: restaurant.name,
      pictureId: restaurant.pictureId,
      city: restaurant.city,
      description: restaurant.description,
      rating: restaurant.rating,
    };

    objectStore.put(restaurantData);
    await transaction.complete;
    console.log("Restoran ditambahkan ke favorit:", restaurantData);
  } catch (error) {
    console.error("Error menambahkan restoran ke favorit:", error);
  }
};

export const removeRestaurantFromFavorites = async (restaurantId) => {
  const db = await openDatabase();
  const transaction = db.transaction(OBJECT_STORE_NAME, "readwrite");
  const objectStore = transaction.objectStore(OBJECT_STORE_NAME);
  objectStore.delete(restaurantId);
  return transaction.complete;
};

export const isRestaurantFavorited = async (restaurantId) => {
  const db = await openDatabase();
  const transaction = db.transaction(OBJECT_STORE_NAME, "readonly");
  const objectStore = transaction.objectStore(OBJECT_STORE_NAME);
  return new Promise((resolve, reject) => {
    const request = objectStore.get(restaurantId);
    request.onsuccess = () => {
      resolve(request.result !== undefined);
    };
    request.onerror = () => {
      reject(request.error);
    };
  });
};

const fetchFavoriteRestaurants = async () => {
  try {
    const db = await openDatabase();
    const transaction = db.transaction(OBJECT_STORE_NAME, "readonly");
    const objectStore = transaction.objectStore(OBJECT_STORE_NAME);

    const request = objectStore.getAll();
    return new Promise((resolve, reject) => {
      request.onsuccess = () => {
        resolve(request.result);
      };

      request.onerror = (event) => {
        console.error("Error saat mengambil restoran favorit:", event.target.error);
        reject(event.target.error);
      };
    });
  } catch (error) {
    console.error("Error saat membuka database:", error);
    throw error;
  }
};

class FavoriteRestaurantList extends HTMLElement {
  async connectedCallback() {
    try {
      const favoriteRestaurants = await fetchFavoriteRestaurants();

      if (favoriteRestaurants && favoriteRestaurants.length > 0) {
        this.renderFavoriteList(favoriteRestaurants);
      } else {
        this.innerHTML = "<p>Anda belum memiliki restoran favorit.</p>";
      }
    } catch (error) {
      this.innerHTML = "<p>Terjadi kesalahan saat mengambil restoran favorit.</p>";
      console.error("Error di FavoriteRestaurantList:", error);
    }
  }

  renderFavoriteList(restaurants) {
    this.innerHTML = `<div class="favorit-grid"></div>`;
    const gridContainer = this.querySelector(".favorit-grid");

    restaurants.forEach((restaurant) => {
      const favElement = document.createElement("div");
      favElement.classList.add("restaurant-item");

      favElement.innerHTML = `
      <div class="list_item">
      <img 
        class="list_item_thumb" 
        src="${RESTAURANT_API_ENDPOINT}/images/medium/${restaurant.pictureId}" 
        alt="${restaurant.name}" 
      />
      <div class="city">${restaurant.city}</div>
      <div class="list_item_content">
        <div class="list_item_rating">
          <span class="star-icon">&#9733;</span> 
          ${restaurant.rating}
        </div>
        <h1 class="list_item_title">
        <a href="#detail-${restaurant.id}" data-id="${restaurant.id}">${restaurant.name}</a>
        </h1>
        <div class="list_item_desc">${restaurant.description.slice(0, 150)}...</div>
      </div>
    </div>
  `;

      gridContainer.appendChild(favElement);
    });
  }
}

customElements.define("favorite-restaurant-list", FavoriteRestaurantList);

