  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Nunito Sans", sans-serif;
}
body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}
body {
  font-size: 12px;
  color: #424242;
  background-color: ivory;
}

a {
  display: inline-block;
  min-width: 44px;
  min-height: 44px;
}

/* ================== */
/* Start Nav */
/* ================== */
.menu-seluler {
  background-color: #864af9;
  color: white;
  width: 100%;
  display: flex;
  font-weight: 600;
  font-size: 20px;
  padding: 10px;
  justify-content: space-between;
}
.nav-seluler {
  background-color: #864af9;
