}
.navlist-seluler {
  width: 60%;
  padding: 0;
  margin: 0;
  text-align: left;
}
.navitems-seluler {
  box-sizing: border-box;
  display: inline-block;
  text-align: right;
  line-height: 24px;
  text-transform: uppercase;
  color: white;
}

.logo-seluler {
  margin-top: 20px;
}
.iconmenu-seluler a {
  text-decoration: none;
  color: white;
  font-size: 49px;
}
.nav-seluler a {
  display: inline-block;
  padding: 1.3em;
  text-decoration: none;
  color: #864af9;
}
.nav-biasa {
  background-color: #864af9;
  width: 100%;
  display: flex;
  font-weight: 600;
  font-size: 14px;
  text-transform: uppercase;
}
.navlist {
  width: 80%;
  padding: 0;
  margin: 0;
  text-align: right;
}
.logo {
  width: 20%;
  padding: 0;
  margin: 0;
  text-align: left;
  box-sizing: border-box;
  display: inline-block;
  line-height: 24px;
}
.navitems {
  box-sizing: border-box;
  display: inline-block;
  text-align: right;
  line-height: 24px;
  text-transform: uppercase;
}
.navitems:hover a {
  color: #ff5733;
}

.nav-biasa a {
  display: inline-block;
  padding: 1.3em;
  text-decoration: none;
  color: white;
}
.nav-biasa a:hover {
  text-decoration: none;
  color: #f8e559;
}
/* ================== */
/* End Nav */
/* ================== */

/* ================== */
/* Start Hero Main */
/* ================== */
.hero {
  position: relative;
  overflow: hidden;
  text-align: center;
  max-height: 500px;
}

.hero-image {
  width: 100%;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.hero-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
}

.hero-text h1 {
  font-size: 50px;
  margin-bottom: 20px;
}

.hero-text p {
  font-size: 1rem;
  margin-bottom: 0;
}
/* ================== */
/* End Hero Main */
/* ================== */

/* ================== */
/* Start Content Main */
/* ================== */
main {
  width: 100%;
  margin: 0 auto;
}

.main-grid {
  display: grid;
  grid-template-columns: 3fr;
  gap: 20px;
}

.content {
  margin: 0 20px;
}

.update {
  width: 100%;
  margin-top: 30px;
  margin-bottom: 50px;
  text-align: center;
}
.update h1 {
  font-weight: 700;
  font-size: 2em;
  margin-bottom: 50px;
}

.restaurant-grid,
.favorit-grid {
  text-align: justify;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 24px;
  padding: 24px;
  box-sizing: border-box;
}

.list_item {
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
}
.city {
  position: absolute;
  top: 8px;
  left: 16px;
  border-radius: 5px;
  background-color: #f8e559;
  padding: 5px 10px 5px;
  color: white;
  font-weight: 500;
}

.restaurant-item {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s;
  overflow: hidden;
}

.list_item_content {
  padding: 16px;
}

.star-icon {
  color: #f8e559;
}

.restaurant-item:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.list_item_thumb {
  width: 100%;
  max-height: 150px;
}
.list_item_rating {
  font-size: 22px;
  text-transform: uppercase;
  color: #999;
  background-color: #864af9;
  width: 79px;
  font-weight: 500;
  border-radius: 5px;
}
.star-icon {
  color: #ffc400;
  font-size: 25px;
  margin-left: 5px;
}
.list_item_rating_value {
  color: #ebe8ff;
  font-weight: bolder;
  text-decoration: none;
  text-align: left;
  padding-top: 5px;
}
.list_item_rating_value a {
  padding-top: 5px;
}
.list_item_title {
  font-weight: 500;
  font-size: 16px;
  margin-top: -10px;
  transition: 0.3s opacity;
}
.list_item_title:hover {
  opacity: 0.5;
}
.list_item_title a {
  text-decoration: none;
  color: inherit;
  padding: 0.8rem 0;
}
#RestaurantList .list_item_desc {
  margin-top: -50px;
  font-size: 12px;
  line-height: 1.5em;
}

#favoriteRestaurantList .list_item_desc {
  font-size: 12px;
  line-height: 1.5em;
}

#restaurantDetail {
  padding: 20px;
  background-color: #f8f8f8;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  margin-top: -30px;
  margin-bottom: 30px;
  margin-left: 40px;
  margin-right: 40px;
  max-width: 100%;
  font-size: 16px;
}

#restaurantDetail h1 {
  text-align: center;
  font-size: 40px;
  background-color: #864af9;
  color: #fff;
  border-radius: 30px;
  margin-bottom: 20px;
}

#restaurantDetail img {
  width: 70%;
  border-radius: 8px;
  margin-bottom: 20px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

#restaurantDetail h2 {
  color: #333;
  font-size: 40px;
  margin-bottom: 10px;
  margin-left: 40px;
  font-weight: 900;
}
.back-button {
  background-color: #f8e559;
  color: #fff;
  border: none;
  width: 70px;
  height: 70px;
  min-width: 44px;
  min-height: 44px;
  cursor: pointer;
  border-radius: 50%;
  margin-left: 40px;
}
.back-button svg {
  min-width: 44px;
  min-height: 44px;
}

.back-button:hover {
  background-color: #6311fc;
}

.detailatas,
.ulasanresto {
  background-color: #ebebeb;
  padding: 15px;
  margin: 20px 40px;
  border-radius: 8px;
}

#restaurantDetail p {
  color: #666;
  margin-bottom: 10px;
  text-align: justify;
}

.favorite-button {
  text-align: right;
  background: none;
  border: none;
  cursor: pointer;
  color: #ff0000;
  font-size: 50px;
  transition: color 0.3s;
}

.favorite-button:hover {
  color: #ff6666;
}

.favorite-button.favorited {
  color: #ff0000;
}

.favorite-button.favorited:hover {
  color: #b80000;
}

.favorite-button.favorited .fa-heart {
  font-weight: bold;
}

#reviewFormContainer {
  margin: 20px 40px;
  background-color: #ebebeb;
  padding: 15px;
  border-radius: 8px;
}

#reviewFormContainer h3 {
  color: rgb(255, 255, 255);
  font-size: 1.5em;
  margin-bottom: 10px;
  text-align: center;
  background-color: #f8e559;
  border-radius: 8px;
  padding: 5px 0;
}

#reviewForm input,
#reviewForm textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px; /* Sudut membulat */
  font-size: 1em; /* Ukuran font */
  margin-bottom: 10px; /* Jarak bawah */
}

/* Tombol untuk mengirim ulasan */
#reviewForm button {
  padding: 10px 20px; /* Ruang dalam */
  min-width: 44px;
  min-height: 44px;
  background-color: #864af9; /* Warna tombol */
  color: #fff; /* Warna teks */
  border: none; /* Tanpa border */
  border-radius: 5px; /* Sudut membulat */
  font-weight: bold; /* Teks tebal */
  cursor: pointer; /* Tampilkan kursor pointer */
  transition: background-color 0.3s; /* Transisi untuk hover */
}

#reviewForm button:hover {
  background-color: #600ff7; /* Warna saat hover */
}
/* ================== */
/* End Content Main */
/* ================== */

/* ================== */
/* Start Riview */
/* ================== */
.ulasanresto h3 {
  color: rgb(255, 255, 255); /* Warna teks gelap */
  font-size: 1.5em; /* Ukuran font */
  margin-bottom: 10px; /* Jarak bawah */
  text-align: center;
  background-color: #f8e559;
  border-radius: 8px;
  padding: 5px 0;
}

/* Tampilan untuk ulasan pelanggan */
.customer-review {
  padding: 10px; /* Ruang dalam */
  background-color: #fff; /* Warna latar belakang */
  border: 1px solid #ddd; /* Warna border */
  border-radius: 5px; /* Sudut membulat */
  margin-top: 10px; /* Jarak atas */
}

.customer-review p {
  margin: 5px 0; /* Margin atas dan bawah */
  line-height: 1.5; /* Tinggi garis */
}

.customer-review strong {
  color: #333; /* Warna teks gelap */
}

.reviews {
  grid-column: 1; /* Menempatkan ulasan di kolom pertama */
  margin-top: 74px;
  margin-left: 20px;
}

.review {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 20px;
  margin-bottom: 20px;
  background-color: #864af9;
}

.review1 {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 20px;
  margin-bottom: 20px;
  background-color: #ffffff;
}

.reviews h1 {
  font-weight: 700;
  font-size: 20px;
  margin-bottom: 2px;
  text-align: center;
  background-color: #f8e559;
  padding: 5px;
  border-radius: 5px;
}

.review1 img {
  border-radius: 50%;
  margin: 0 5px 0px 0;
}

.review-header {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.customer-image {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 10px;
}

.customer-name {
  margin: 0;
}

.review-date {
  margin: 0;
  color: #888;
  font-size: 14px;
}
.review-rating {
  color: #f8e559;
  font-size: 20px;
}
.review-text {
  margin-top: 10px;
  line-height: 1.5;
}

.skip-link {
  position: absolute;
  top: -100px;
  background-color: #ebe8ff;
  color: #864af9;
  margin-left: 10px;
  padding: 10px;
  font-size: 20px;
  z-index: 100;
  text-decoration: none;
}
.skip-link:focus {
  top: 100px;
}

#backToHome {
  display: block; /* Pastikan tombol terlihat */
}
/* ================== */
/* End Riview */
/* ================== */

/* ================== */
/* Start Footer */
/* ================== */
.footer {
  background-color: #864af9;
  color: #fff;
  padding: 40px 0;
  margin-top: 15px;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 1200px;
  margin: 0 auto;
}

.footer-content {
  display: flex;
  flex-direction: row;
  flex: 1;
}

.footer-logo {
  font-size: 24px;
  margin-bottom: 20px;
}

.footer-description {
  flex-grow: 1;
  flex-basis: 80%;
}

.footer-menu {
  flex-grow: 1;
  flex-basis: 10%;
  list-style-type: none;
  padding: 0;
  margin: 0;
  text-align: right;
}

.footer-menu a {
  color: #fff;
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-menu a:hover {
  color: #ccc;
}

@media screen and (max-width: 1200px) {
  .footer-content {
    flex-direction: column;
    align-items: center;
  }

  .footer-logo {
    flex-basis: auto;
  }

  .footer-description,
  .footer-menu {
    display: none;
  }
}
/* ================== */
/* Footer End */
/* ================== */

/* ================== */
/* Splash Screen */
/* ================== */
#splashScreen {
  position: fixed;
  color: #fff;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #864af9; /* Warna splash screen */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 1000; /* Pastikan berada di atas konten utama */
}

.splash-logo {
  width: 100px; /* Ukuran logo */
}

#mainContent {
  display: none; /* Sembunyikan konten utama hingga splash screen hilang */
}

.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  backdrop-filter: blur(50px);
  -webkit-backdrop-filter: blur(50px);
  z-index: 9999; /* Z-index yang tinggi untuk menutupi semua elemen lainnya */
  transition: opacity 0.75s, visibility 0.75s;
}

.loader--hidden {
  opacity: 0;
  visibility: hidden;
}

.loader::after {
  content: "";
  width: 75px;
  height: 75px;
  border: 15px solid #dddddd;
  border-top-color: #864af9;
  border-radius: 50%;
  animation: loading 0.75s ease infinite;
}

.responsive-hero {
  width: 100%; /* Membuat gambar mengambil lebar penuh kontainernya */
  height: auto; /* Membuat tinggi gambar menyesuaikan secara proporsional */
  max-width: 100%; /* Membuat gambar tidak melebihi ukuran kontainernya */
}

@keyframes loading {
  from {
    transform: rotate(0turn);
  }
  to {
    transform: rotate(1turn);
  }
}

@media screen and (min-width: 500px) {
  .menu-seluler {
    display: none;
  }
  .nav-seluler {
    display: none;
  }
}
@media screen and (max-width: 499px) {
  .restaurant-grid,
  .favorit-grid {
    grid-template-columns: 1fr;
    gap: 16px;
    padding: 16px;
    width: 80%;
    margin-left: 10%;
  }
  .list_item_thumb {
    max-height: 200px; /* Tinggi gambar lebih besar untuk layar kecil */
  }

  .content {
    margin-left: 10px;
    margin-right: 10px;
  }
  .main-grid {
    display: flex;
    flex-direction: column-reverse;
  }
  picture {
    height: 37%;
  }
  .reviews {
    margin-top: 20px; /* Ubah margin sesuai kebutuhan */
    margin-bottom: 20px; /* Ubah margin sesuai kebutuhan */
    margin-left: auto;
    margin-right: auto;
  }
  .reviews h1 {
    text-align: center;
  }

  .content {
    margin-top: 20px; /* Ubah margin sesuai kebutuhan */
    margin-bottom: 20px; /* Ubah margin sesuai kebutuhan */
    margin-left: 10px;
    margin-right: 10px;
    grid-column: initial; /* Hapus aturan grid-column */
  }
  .nav-biasa {
    display: none;
  }
  .nav-seluler {
    z-index: 10;
    background-color: #fff;
    width: 100%;
    position: absolute;
    -webkit-transform: translate(-100%, 0);
    transform: translate(-100%, 0);
    transition: transform 0.3s ease;
  }
  .open {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }
  .navitems-seluler {
    display: list-item;
    color: black;
    width: 100%;
    text-align: left;
  }
  .list {
    grid-template-columns: 1fr; /* Mengatur satu kolom untuk layar kecil */
  }
}

@media screen and (max-width: 768px) {
  .hero-text h1 {
    font-size: 30px;
    margin-bottom: 15px;
  }

  .hero-text p {
    display: none;
  }
}
