play: flex;\n  font-weight: 600;\n  font-size: 20px;\n  padding: 10px;\n  justify-content: space-between;\n}\n.nav-seluler {\n  background-color: #864af9;\n  width: 100%;\n  display: flex;\n  font-weight: 600;\n  font-size: 14px;\n  text-transform: uppercase;\n}\n.navlist-seluler {\n  width: 60%;\n  padding: 0;\n  margin: 0;\n  text-align: left;\n}\n.navitems-seluler {\n  box-sizing: border-box;\n  display: inline-block;\n  text-align: right;\n  line-height: 24px;\n  text-transform: uppercase;\n  color: white;\n}\n\n.logo-seluler {\n  margin-top: 20px;\n}\n.iconmenu-seluler a {\n  text-decoration: none;\n  color: white;\n  font-size: 49px;\n}\n.nav-seluler a {\n  display: inline-block;\n  padding: 1.3em;\n  text-decoration: none;\n  color: #864af9;\n}\n.nav-biasa {\n  background-color: #864af9;\n  width: 100%;\n  display: flex;\n  font-weight: 600;\n  font-size: 14px;\n  text-transform: uppercase;\n}\n.navlist {\n  width: 80%;\n  padding: 0;\n  margin: 0;\n  text-align: right;\n}\n.logo {\n  width: 20%;\n  padding: 0;\n  margin: 0;\n  text-align: left;\n  box-sizing: border-box;\n  display: inline-block;\n  line-height: 24px;\n}\n.navitems {\n  box-sizing: border-box;\n  display: inline-block;\n  text-align: right;\n  line-height: 24px;\n  text-transform: uppercase;\n}\n.navitems:hover a {\n  color: #ff5733;\n}\n\n.nav-biasa a {\n  display: inline-block;\n  padding: 1.3em;\n  text-decoration: none;\n  color: white;\n}\n.nav-biasa a:hover {\n  text-decoration: none;\n  color: #f8e559;\n}\n/* ================== */\n/* End Nav */\n/* ================== */\n\n/* ================== */\n/* Start Hero Main */\n/* ================== */\n.hero {\n  position: relative;\n  overflow: hidden;\n  text-align: center;\n  max-height: 500px;\n}\n\n.hero-image {\n  width: 100%;\n}\n\n.overlay {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background-color: rgba(0, 0, 0, 0.5);\n}\n\n.hero-text {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  t