var heroText = document.querySelector(".hero-text h1");
var textContent = "Welcome to Chykoo Apps!";

function typeWriter() {
  var i = 0;
  var speed = 50;
  var interval = setInterval(function () {
    if (i < textContent.length) {
      heroText.textContent += textContent.charAt(i);
      i++;
    } else {
      clearInterval(interval);
    }
  }, speed);
}

document.addEventListener("DOMContentLoaded", function () {
  typeWriter();
});
